import React from "react";
import "../../node_modules/gestalt/dist/gestalt.css"
// import { Form, TextArea, Button, Icon } from "gestalt";
import {Form, TextArea, Button} from 'semantic-ui-react';
import { useState, useEffect } from "react";
import axios from "axios";

export default function InputField(props) {
  const [InputText, setInputText] = React.useState('');
  const [detectLanguageKey, setdetectedLanguageKey] = useState('');
  const [selectedLanguageKey, setLanguageKey] = useState('');
  const [resultText, setResultText] = useState('');
  const [LanguagesList, setLanguagesList] = useState(null);  

  const getLanguageHeaders = {
    method: 'GET',
    url: 'https://translate-plus.p.rapidapi.com/',
    headers: {
      'X-RapidAPI-Key': '982038c41dmsh15962e8b61649fdp16f3b0jsn91aa919e4c13',
      'X-RapidAPI-Host': 'translate-plus.p.rapidapi.com'
    }
  };

  const detectLanguageHeaders = {
    method: 'POST',
    url: 'https://translate-plus.p.rapidapi.com/language_detect',
    headers: {
      'content-type': 'application/json',
      'X-RapidAPI-Key': '982038c41dmsh15962e8b61649fdp16f3b0jsn91aa919e4c13',
      'X-RapidAPI-Host': 'translate-plus.p.rapidapi.com'
    },
    data: `{"text":"${InputText}"}`
  };

  const translateHeaders = {
    method: 'POST',
    url: 'https://translate-plus.p.rapidapi.com/translate',
    headers: {
      'content-type': 'application/json',
      'X-RapidAPI-Key': '982038c41dmsh15962e8b61649fdp16f3b0jsn91aa919e4c13',
      'X-RapidAPI-Host': 'translate-plus.p.rapidapi.com'
    },
    data: `{"text": "${InputText}", "source":"${detectLanguageKey}","target":"${selectedLanguageKey}"}`
  };

  // detect the language of the input text
  const getLanguageSource = () => {
    console.log(detectLanguageHeaders)
    axios.request(detectLanguageHeaders)
    .then(function(response) {
      console.log(response.data)
      setdetectedLanguageKey(response.data.language_detection.language)
    }).catch(function (error) {
      // console.error(error);
    })
  }

  // fetch the supported language list from the api
  useEffect(() => {
    axios.request(getLanguageHeaders).then(response => {
      console.log(response.data);
      setLanguagesList(response.data.supported_languages)
    }).catch(error => {
      // console.error(error)
    });
  }, []);

  const languageKey = (selectedLanguage) => {
    setLanguageKey(selectedLanguage.target.value)
  }

  const translateText = () => {
    setResultText(InputText);
    getLanguageSource();
    axios.request(translateHeaders).then(function(response){
      setResultText(response.data.translations.translation)
    })
  }

  useEffect(() => {
    axios.request(getLanguageHeaders)
    .then((response) => {
     setLanguagesList(response.data.supported_languages)
    })

    getLanguageSource()
  }, [InputText])

  return (
    <>
    <div className="translate">
      <Form>
        <Form.Group widths='equal'>
        <Form.Field
          control={TextArea}
          placeholder='Type Text to Translate..'
          onChange={(e) => setInputText(e.target.value)}
          row='10'
        />
        <select onChange={languageKey}>
          {LanguagesList && Object.entries(LanguagesList).map(([key, value]) => {
          return(
            <option value={value}>{key}</option>
          )
          })}
        </select>
        <Button
            color="orange"
            size="large"
            onClick={translateText}
            >
        Translate</Button>
        <Form.Field
          control={TextArea}
          placeholder='Your Result Translation..'
          value={resultText}
        />
        </Form.Group>
      </Form>
  </div>
</>
  );
}